                    </div>
                  </li>
                  <div class="header-account-cart-list-total">
                    <div>總金額: {{ cart.final_total }}</div>
                    <div>
                      <router-link to="/cart" class="main-btn"
                        >結帳去</router-link
                      >
                    </div>
                  </div>
