                <ul v-else>
                  <li class="nocart">目前沒有任何購物車商品</li>
                </ul>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="header-mobile-btn">
        <button @click="showMenu">
          <span></span>
          <span></span>
          <span></span>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
export default {
  name: 'headers',
  inject: ['reload'],
  data () {
    return {
      cartShow: false,
      login: false,
      productMobileShow: false
    }
  },
  methods: {
    ...mapActions('products', ['getCart']),
    delCart (id) {
      this.$store.dispatch('products/delCart', id)
    },
    cartList () {
      let windowWidth = window.innerWidth
      if (windowWidth >= 768) {
        this.cartShow = !this.cartShow
      } else {
        this.$router.push('/cart')
      }
    },
    checkLogin () {
      const vm = this
      const url = `${process.env.VUE_APP_API}/api/user/check`
      this.$http.post(url).then(res => {
        if (res.data.success) {
          vm.login = true
        }
      })
    },
    showMenu () {
      let menu = document.getElementById('menuShow')
      let btn = document.querySelector('.header-mobile-btn')
      if (menu.style.transform === '' || menu.style.transform === 'translateY(-100%)') {
        menu.style.transform = 'translateY(0%)'
        btn.classList.add('mobile-btn-active')
      } else {
        menu.style.transform = 'translateY(-100%)'
        btn.classList.remove('mobile-btn-active')
      }
    },
    mobileShow () {
      let windowWidth = window.innerWidth
      if (windowWidth <= 768) {
        this.productMobileShow = !this.productMobileShow
      }
    },
    pageload (page) {
      this.$router.push(`/products/${page}`)
    }
  },
  computed: {
    ...mapGetters('products', ['cart', 'cartlength'])
  },
  watch: {
    // 監聽路由變更時，執行函式
    $route () {
      this.reload()
    }
  },
  mounted () {
    $(window).scroll(function () {
      let thisScroll = $(window).scrollTop()
      const header = $('.header')
      thisScroll > 100 ? header.addClass('active') : header.removeClass('active')
    })
    this.getCart()
  },
  created () {
    this.checkLogin()
  }
}
</script>
